import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useAdmin } from './hooks/useAdmin.js';

export const AdminDashboardPage = () => {
  const navigate = useNavigate();
  const { stats, loading, error } = useAdmin();

  console.log('AdminDashboardPage render:', { stats, loading, error });

  if (loading && !stats) {
    return (
      <div className="toss-dashboard">
        <div style={{ textAlign: 'center', padding: '40px' }}>
          <div className="toss-loading" style={{ margin: '0 auto 16px' }}></div>
          <p style={{ color: '#64748b' }}>   ...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="toss-dashboard">
        <div className="toss-error" style={{ maxWidth: '600px', margin: '0 auto' }}>
          <h3>  </h3>
          <p>{error}</p>
          <button 
            className="toss-button" 
            onClick={() => window.location.reload()}
            style={{ marginTop: '16px' }}
          >
             
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="toss-dashboard">
      <h1 className="toss-page-title">
         
      </h1>
      <p className="toss-page-subtitle">
              .
      </p>

      {/*   */}
      <div className="toss-grid" style={{ marginBottom: '40px' }}>
        <div className="toss-card-dashboard">
          <h2 className="toss-card-title"> </h2>
          <div className="toss-card-stats">
            {stats?.totalUsers || 0}
          </div>
          <p className="toss-card-content">
               
          </p>
          <div style={{ 
            fontSize: '14px', 
            color: '#94a3b8',
            marginTop: '8px'
          }}>
            : {stats?.adminUsers || 0} | : {stats?.regularUsers || 0}
          </div>
        </div>

        <div className="toss-card-dashboard">
          <h2 className="toss-card-title"> </h2>
          <div className="toss-card-stats">
            {stats?.totalTests || 0}
          </div>
          <p className="toss-card-content">
               
          </p>
          <div style={{ 
            fontSize: '14px', 
            color: '#94a3b8',
            marginTop: '8px'
          }}>
            : {stats?.totalCoreTests || 0} | : {stats?.totalDailyTests || 0}
          </div>
        </div>

        <div className="toss-card-dashboard">
          <h2 className="toss-card-title">  </h2>
          <div className="toss-card-stats">
            {stats?.pendingTests || 0}
          </div>
          <p className="toss-card-content">
               
          </p>
          <div style={{ 
            fontSize: '14px', 
            color: '#94a3b8',
            marginTop: '8px'
          }}>
            : {stats?.pendingRate || 0}%
          </div>
        </div>
      </div>

      {/*    */}
      <div className="toss-grid">
        <div className="toss-card-dashboard">
          <h2 className="toss-card-title"> </h2>
          <p className="toss-card-content">
                 .
          </p>
          <button 
            className="toss-button"
            style={{ 
              display: 'inline-block', 
              marginTop: '16px',
              textDecoration: 'none',
              textAlign: 'center'
            }}
            onClick={() => navigate('/admin/users')}
          >
              
          </button>
        </div>

        <div className="toss-card-dashboard">
          <h2 className="toss-card-title">  </h2>
          <p className="toss-card-content">
                /  .
          </p>
          <button 
            className="toss-button"
            style={{ 
              display: 'inline-block', 
              marginTop: '16px',
              textDecoration: 'none',
              textAlign: 'center'
            }}
            onClick={() => navigate('/admin/tests')}
          >
              
          </button>
        </div>
      </div>
    </div>
  );
}; 