import { useState, useEffect, useCallback } from 'react';
import { useAuth } from '../../../shared/hooks/useAuth.js';
import { serviceContainer } from '../../../infrastructure/services/ServiceContainer.js';

export const useAdmin = () => {
  const { user } = useAuth();
  const [stats, setStats] = useState(null);
  const [users, setUsers] = useState([]);
  const [pendingTests, setPendingTests] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const adminUseCase = serviceContainer.getAdminUseCase();

  //   
  const fetchStats = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      
      console.log('Fetching admin stats...');
      const result = await adminUseCase.getAdminStats();
      console.log('Admin stats result:', result);
      
      if (result.success) {
        setStats(result.data);
      } else {
        setError(result.error || '   .');
      }
    } catch (err) {
      console.error('Error in fetchStats:', err);
      setError('   .');
    } finally {
      setLoading(false);
    }
  }, [adminUseCase]);

  //   
  const fetchUsers = useCallback(async (page = 1, limit = 10, filters = {}) => {
    try {
      setLoading(true);
      setError(null);
      
      const result = await adminUseCase.getAllUsers(page, limit, filters);
      if (result.success) {
        setUsers(result.data.users);
        return result.data.total;
      } else {
        setError(result.error);
        return 0;
      }
    } catch (err) {
      setError('   .');
      return 0;
    } finally {
      setLoading(false);
    }
  }, [adminUseCase]);

  //    
  const fetchPendingTests = useCallback(async (page = 1, limit = 10) => {
    try {
      setLoading(true);
      setError(null);
      
      const result = await adminUseCase.getPendingTests(page, limit);
      if (result.success) {
        setPendingTests(result.data.tests);
        return result.data.total;
      } else {
        setError(result.error);
        return 0;
      }
    } catch (err) {
      setError('    .');
      return 0;
    } finally {
      setLoading(false);
    }
  }, [adminUseCase]);

  //   
  const updateUserRole = useCallback(async (userId, newRole) => {
    try {
      setError(null);
      
      const result = await adminUseCase.updateUserRole(userId, newRole);
      if (result.success) {
        //   
        await fetchUsers();
        //  
        await fetchStats();
        return true;
      } else {
        setError(result.error);
        return false;
      }
    } catch (err) {
      setError('   .');
      return false;
    }
  }, [adminUseCase, fetchUsers, fetchStats]);

  //   
  const updateUserStatus = useCallback(async (userId, newStatus) => {
    try {
      setError(null);
      
      const result = await adminUseCase.updateUserStatus(userId, newStatus);
      if (result.success) {
        //   
        await fetchUsers();
        //  
        await fetchStats();
        return true;
      } else {
        setError(result.error);
        return false;
      }
    } catch (err) {
      setError('   .');
      return false;
    }
  }, [adminUseCase, fetchUsers, fetchStats]);

  //  
  const deleteUser = useCallback(async (userId) => {
    try {
      setError(null);
      
      const result = await adminUseCase.deleteUser(userId);
      if (result.success) {
        //   
        await fetchUsers();
        //  
        await fetchStats();
        return true;
      } else {
        setError(result.error);
        return false;
      }
    } catch (err) {
      setError('  .');
      return false;
    }
  }, [adminUseCase, fetchUsers, fetchStats]);

  //  
  const createTester = useCallback(async (testerData) => {
    try {
      setError(null);
      
      const result = await adminUseCase.createTester(testerData);
      if (result.success) {
        //   
        await fetchUsers();
        //  
        await fetchStats();
        return true;
      } else {
        setError(result.error);
        return false;
      }
    } catch (err) {
      setError('  .');
      return false;
    }
  }, [adminUseCase, fetchUsers, fetchStats]);

  //  
  const approveTest = useCallback(async (testId, comment = '') => {
    try {
      setError(null);
      
      const result = await adminUseCase.approveTest(testId, user.id, comment);
      if (result.success) {
        //     
        await fetchPendingTests();
        //  
        await fetchStats();
        return true;
      } else {
        setError(result.error);
        return false;
      }
    } catch (err) {
      setError('  .');
      return false;
    }
  }, [adminUseCase, user?.id, fetchPendingTests, fetchStats]);

  //  
  const rejectTest = useCallback(async (testId, comment = '') => {
    try {
      setError(null);
      
      const result = await adminUseCase.rejectTest(testId, user.id, comment);
      if (result.success) {
        //     
        await fetchPendingTests();
        //  
        await fetchStats();
        return true;
      } else {
        setError(result.error);
        return false;
      }
    } catch (err) {
      setError('  .');
      return false;
    }
  }, [adminUseCase, user?.id, fetchPendingTests, fetchStats]);

  //   
  useEffect(() => {
    if (user?.isAdmin) {
      fetchStats();
    }
  }, [user, fetchStats]);

  //     
  useEffect(() => {
    const handleFocus = () => {
      if (user?.isAdmin) {
        console.log('Page focused, refreshing admin stats...');
        fetchStats();
      }
    };

    window.addEventListener('focus', handleFocus);
    return () => window.removeEventListener('focus', handleFocus);
  }, [user, fetchStats]);

  return {
    // 
    stats,
    users,
    pendingTests,
    loading,
    error,
    
    // 
    fetchStats,
    fetchUsers,
    fetchPendingTests,
    updateUserRole,
    updateUserStatus,
    deleteUser,
    createTester,
    approveTest,
    rejectTest,
    
    // 
    clearError: () => setError(null)
  };
}; 