import { supabase } from '../supabase/client.js';
import { validateFile, compressImage, generateSafeFileName } from '../../shared/utils/fileUtils.js';

export class FileUploadService {
  constructor() {
    this.bucketName = 'test-uploads';
  }

  /**
   *   ( )
   * @param {File} file -  
   * @param {string} path -  
   * @param {Object} options - 
   * @returns {Promise<Object>}  
   */
  async uploadFile(file, path, options = {}) {
    try {
      console.log('   :', {
        fileName: file.name,
        fileSize: file.size,
        fileType: file.type,
        path: path
      });

      //  
      const validation = validateFile(file);
      if (!validation.isValid) {
        console.error('   :', validation.error);
        return { success: false, error: validation.error };
      }

      //   ()
      let fileToUpload = file;
      if (options.compress && file.type.startsWith('image/')) {
        try {
          console.log('   ...');
          fileToUpload = await compressImage(
            file, 
            options.maxWidth || 1920, 
            options.quality || 0.8
          );
          console.log('   :', {
            originalSize: file.size,
            compressedSize: fileToUpload.size
          });
        } catch (error) {
          console.warn('   ,   :', error);
        }
      }

      //   
      const safeFileName = generateSafeFileName(fileToUpload.name, options.prefix || '');
      const fullPath = `${path}/${safeFileName}`;
      
      console.log('   :', {
        originalName: fileToUpload.name,
        safeFileName: safeFileName,
        fullPath: fullPath
      });

      // Supabase Storage 
      console.log(' Supabase Storage  ...');
      const { error } = await supabase.storage
        .from(this.bucketName)
        .upload(fullPath, fileToUpload, {
          cacheControl: '3600',
          upsert: false,
          contentType: fileToUpload.type
        });

      if (error) {
        console.error(' Storage upload error:', error);
        console.error(' Error details:', {
          message: error.message,
          statusCode: error.statusCode,
          error: error.error
        });
        return { success: false, error: `  : ${error.message}` };
      }

      //  URL 
      const { data: urlData } = supabase.storage
        .from(this.bucketName)
        .getPublicUrl(fullPath);

      console.log('   :', {
        path: fullPath,
        url: urlData.publicUrl,
        size: fileToUpload.size
      });

      return {
        success: true,
        data: {
          path: fullPath,
          url: urlData.publicUrl,
          size: fileToUpload.size,
          type: fileToUpload.type,
          name: fileToUpload.name
        }
      };

    } catch (error) {
      console.error('File upload error:', error);
      return { success: false, error: '    .' };
    }
  }

  /**
   *    
   * @param {Array} files -  
   * @param {string} basePath -  
   * @param {Object} options - 
   * @returns {Promise<Array>}   
   */
  async uploadMultipleFiles(files, basePath, options = {}) {
    const uploadPromises = files.map((file, index) => {
      const filePath = `${basePath}/file_${index + 1}`;
      return this.uploadFile(file, filePath, options);
    });

    return await Promise.all(uploadPromises);
  }

  /**
   *  
   * @param {string} path -   
   * @returns {Promise<Object>}  
   */
  async deleteFile(path) {
    try {
      const { error } = await supabase.storage
        .from(this.bucketName)
        .remove([path]);

      if (error) {
        console.error('File deletion error:', error);
        return { success: false, error: '  .' };
      }

      return { success: true };
    } catch (error) {
      console.error('File deletion error:', error);
      return { success: false, error: '    .' };
    }
  }

  /**
   *   
   * @param {string} path -  
   * @returns {Promise<Object>}  
   */
  async getFileInfo(path) {
    try {
      const { data, error } = await supabase.storage
        .from(this.bucketName)
        .list(path.split('/').slice(0, -1).join('/'));

      if (error) {
        console.error('File info error:', error);
        return { success: false, error: '   .' };
      }

      const fileName = path.split('/').pop();
      const fileInfo = data.find(file => file.name === fileName);

      if (!fileInfo) {
        return { success: false, error: '   .' };
      }

      return { success: true, data: fileInfo };
    } catch (error) {
      console.error('File info error:', error);
      return { success: false, error: '     .' };
    }
  }

  /**
   *   URL 
   * @param {string} path -  
   * @returns {string}  URL
   */
  getDownloadUrl(path) {
    const { data } = supabase.storage
      .from(this.bucketName)
      .getPublicUrl(path);
    
    return data.publicUrl;
  }
}

//  
export const fileUploadService = new FileUploadService(); 