import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAdmin } from '../hooks/useAdmin.js';

export const UserManagementPage = () => {
  const navigate = useNavigate();
  const { 
    users, 
    loading, 
    error, 
    fetchUsers, 
    updateUserRole, 
    updateUserStatus, 
    deleteUser,
    clearError 
  } = useAdmin();

  const [currentPage] = useState(1);
  const [filters, setFilters] = useState({
    role: '',
    status: '',
    email: ''
  });

  useEffect(() => {
    fetchUsers(currentPage, 10, filters);
  }, [currentPage, filters, fetchUsers]);

  const handleRoleChange = async (userId, newRole) => {
    const success = await updateUserRole(userId, newRole);
    if (success) {
      alert('  .');
    }
  };

  const handleStatusChange = async (userId, newStatus) => {
    const success = await updateUserStatus(userId, newStatus);
    if (success) {
      alert('  .');
    }
  };

  const handleDeleteUser = async (userId) => {
    if (window.confirm('   ?')) {
      const success = await deleteUser(userId);
      if (success) {
        alert(' .');
      }
    }
  };

  if (loading && users.length === 0) {
    return (
      <div className="toss-dashboard">
        <div style={{ textAlign: 'center', padding: '40px' }}>
          <div className="toss-loading" style={{ margin: '0 auto 16px' }}></div>
          <p style={{ color: '#64748b' }}>   ...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="toss-dashboard">
      <div className="toss-page-header">
        <div className="toss-page-title-section">
          <h1 className="toss-page-title"> </h1>
          <p className="toss-page-subtitle">
                 .
          </p>
        </div>
        <button
          onClick={() => navigate('/admin/create-tester')}
          className="toss-button"
          style={{ padding: '12px 24px' }}
        >
          +   
        </button>
      </div>

      {error && (
        <div className="toss-error" style={{ marginBottom: '24px' }}>
          {error}
          <button 
            onClick={clearError}
            style={{ 
              marginLeft: '10px', 
              background: 'none', 
              border: 'none', 
              color: '#dc2626', 
              cursor: 'pointer',
              fontSize: '16px'
            }}
          >
            
          </button>
        </div>
      )}

      {/*   */}
      <div className="toss-card-dashboard" style={{ marginBottom: '24px' }}>
        <h3 style={{ marginBottom: '16px', color: '#1e293b', fontSize: '18px', fontWeight: '600' }}></h3>
        <div className="toss-filter-grid">
          <select
            value={filters.role}
            onChange={(e) => setFilters(prev => ({ ...prev, role: e.target.value }))}
            className="toss-form-input"
          >
            <option value=""> </option>
            <option value="admin"></option>
            <option value="user"> </option>
          </select>

          <select
            value={filters.status}
            onChange={(e) => setFilters(prev => ({ ...prev, status: e.target.value }))}
            className="toss-form-input"
          >
            <option value=""> </option>
            <option value="active"></option>
            <option value="inactive"></option>
            <option value="suspended"></option>
          </select>

          <input
            type="text"
            placeholder=" ..."
            value={filters.email}
            onChange={(e) => setFilters(prev => ({ ...prev, email: e.target.value }))}
            className="toss-form-input"
          />
        </div>
      </div>

      {/*   */}
      <div className="toss-table">
        {/*   */}
        <div className="toss-table-desktop">
          <div className="toss-table-header">
            <div style={{ 
              display: 'grid', 
              gridTemplateColumns: '2fr 1fr 1fr 1fr 1fr 1fr 1fr', 
              gap: '16px', 
              alignItems: 'center' 
            }}>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div> </div>
              <div> </div>
              <div></div>
            </div>
          </div>
          
          {users.map((user) => (
            <div key={user.id} className="toss-table-row">
              <div style={{ 
                display: 'grid', 
                gridTemplateColumns: '2fr 1fr 1fr 1fr 1fr 1fr 1fr', 
                gap: '16px', 
                alignItems: 'center' 
              }}>
                <div style={{ fontWeight: '500', color: '#1e293b' }}>{user.email}</div>
                <div>
                  <select
                    value={user.role}
                    onChange={(e) => handleRoleChange(user.id, e.target.value)}
                    className="toss-form-input"
                    style={{ padding: '8px 12px', fontSize: '14px' }}
                  >
                    <option value="user"> </option>
                    <option value="admin"></option>
                  </select>
                </div>
                <div>
                  <select
                    value={user.status}
                    onChange={(e) => handleStatusChange(user.id, e.target.value)}
                    className="toss-form-input"
                    style={{ padding: '8px 12px', fontSize: '14px' }}
                  >
                    <option value="active"></option>
                    <option value="inactive"></option>
                    <option value="suspended"></option>
                  </select>
                </div>
                <div style={{ color: '#64748b', fontSize: '14px' }}>{user.formattedCreatedAt}</div>
                <div style={{ color: '#64748b', fontSize: '14px' }}>{user.formattedLastLogin}</div>
                <div style={{ color: '#64748b', fontSize: '14px' }}>{user.testCount}</div>
                <div>
                  <button
                    onClick={() => handleDeleteUser(user.id)}
                    className="toss-button"
                    style={{ 
                      padding: '8px 16px', 
                      fontSize: '14px',
                      background: '#dc2626',
                      border: '1px solid #dc2626'
                    }}
                  >
                    
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>

        {/*   */}
        <div className="toss-table-mobile">
          {users.map((user) => (
            <div key={user.id} className="toss-mobile-card">
              <div className="toss-mobile-card-header">
                <h4 className="toss-mobile-card-title">{user.email}</h4>
                <span className={`toss-mobile-card-badge ${user.role === 'admin' ? 'admin' : 'user'}`}>
                  {user.role === 'admin' ? '' : ''}
                </span>
              </div>

              <div className="toss-mobile-card-info">
                <div>: {user.formattedCreatedAt}</div>
                <div> : {user.testCount}</div>
              </div>

              <div className="toss-mobile-card-form">
                <div className="toss-mobile-card-form-group">
                  <label className="toss-mobile-card-label"></label>
                  <select
                    value={user.role}
                    onChange={(e) => handleRoleChange(user.id, e.target.value)}
                    className="toss-form-input"
                    style={{ padding: '8px 12px', fontSize: '14px' }}
                  >
                    <option value="user"> </option>
                    <option value="admin"></option>
                  </select>
                </div>
                <div className="toss-mobile-card-form-group">
                  <label className="toss-mobile-card-label"></label>
                  <select
                    value={user.status}
                    onChange={(e) => handleStatusChange(user.id, e.target.value)}
                    className="toss-form-input"
                    style={{ padding: '8px 12px', fontSize: '14px' }}
                  >
                    <option value="active"></option>
                    <option value="inactive"></option>
                    <option value="suspended"></option>
                  </select>
                </div>
              </div>

              <div className="toss-mobile-card-actions">
                <button
                  onClick={() => handleDeleteUser(user.id)}
                  className="toss-button"
                  style={{ 
                    padding: '8px 16px', 
                    fontSize: '14px',
                    background: '#dc2626',
                    border: '1px solid #dc2626'
                  }}
                >
                  
                </button>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}; 