//     

export const FILE_TYPES = {
  IMAGE: ['image/jpeg', 'image/jpg', 'image/png', 'image/webp'],
  ALLOWED: ['image/jpeg', 'image/jpg', 'image/png', 'image/webp']
};

export const FILE_SIZE_LIMITS = {
  MAX_SIZE_MB: 10, // 10MB
  MAX_SIZE_BYTES: 10 * 1024 * 1024
};

/**
 *   
 * @param {File} file -  
 * @returns {Object}   { isValid: boolean, error?: string }
 */
export const validateFile = (file) => {
  if (!file) {
    return { isValid: false, error: ' .' };
  }

  //   
  if (file.size > FILE_SIZE_LIMITS.MAX_SIZE_BYTES) {
    return { 
      isValid: false, 
      error: `  ${FILE_SIZE_LIMITS.MAX_SIZE_MB}MB  .` 
    };
  }

  //   
  if (!FILE_TYPES.ALLOWED.includes(file.type)) {
    return { 
      isValid: false, 
      error: 'JPG, PNG, WebP     .' 
    };
  }

  return { isValid: true };
};

/**
 *      
 * @param {number} bytes -   
 * @returns {string}   
 */
export const formatFileSize = (bytes) => {
  if (bytes === 0) return '0 Bytes';
  
  const k = 1024;
  const sizes = ['Bytes', 'KB', 'MB', 'GB'];
  const i = Math.floor(Math.log(bytes) / Math.log(k));
  
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
};

/**
 *   
 * @param {string} filename - 
 * @returns {string} 
 */
export const getFileExtension = (filename) => {
  return filename.slice((filename.lastIndexOf('.') - 1 >>> 0) + 2);
};

/**
 *   
 * @param {string} originalName -  
 * @param {string} prefix - 
 * @returns {string}  
 */
export const generateSafeFileName = (originalName, prefix = '') => {
  const timestamp = Date.now();
  
  // , ,    / 
  const safeName = originalName
    .replace(/[^a-zA-Z0-9.-]/g, '_')  //   
    .replace(/_+/g, '_')              //   
    .replace(/^_|_$/g, '')            //   
    .toLowerCase();                   //  
  
  //    
  const finalName = safeName || 'image';
  
  return `${prefix}${timestamp}_${finalName}`;
};

/**
 *    ( )
 * @param {File} file -   
 * @param {number} maxWidth -  
 * @param {number} quality -  (0-1)
 * @returns {Promise<File>}  
 */
export const compressImage = (file, maxWidth = 1920, quality = 0.8) => {
  return new Promise((resolve, reject) => {
    const canvas = document.createElement('canvas');
    const ctx = canvas.getContext('2d');
    const img = new Image();
    
    img.onload = () => {
      //    
      const ratio = Math.min(maxWidth / img.width, maxWidth / img.height);
      const newWidth = img.width * ratio;
      const newHeight = img.height * ratio;
      
      canvas.width = newWidth;
      canvas.height = newHeight;
      
      //  
      ctx.drawImage(img, 0, 0, newWidth, newHeight);
      
      //   
      canvas.toBlob(
        (blob) => {
          const compressedFile = new File([blob], file.name, {
            type: file.type,
            lastModified: Date.now()
          });
          resolve(compressedFile);
        },
        file.type,
        quality
      );
    };
    
    img.onerror = () => {
      reject(new Error('  .'));
    };
    
    img.src = URL.createObjectURL(file);
  });
};

/**
 *    
 * @param {number} loaded -  
 * @param {number} total -  
 * @returns {number}  (0-100)
 */
export const calculateProgress = (loaded, total) => {
  return Math.round((loaded / total) * 100);
}; 